#header {
  width: 100%;
  max-width: 1050px;
  margin: 0 auto;
  &:hover {
    transition: all 0.6s ;
  }
  .container {
    .gap {
      width: 100%;
      display: flex;
      justify-content: space-between;
      .logo {
        width: 225px;
        a {
          display: block;
          img {
            width: 5rem;
          }
        }
      }
      .menu_btn {
        width: 825px;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        justify-items: center;
        left: 30%;
        ul {
          display: flex;
          justify-content:space-evenly;
          li {
            width: 150px;
            list-style: none;
            padding: 5px;
            font-size: 20px;
            font-weight: bold;
            color: white;
            cursor: pointer;
          }
        }
      }
    }
  }
}