#notice {
    width: 100%;
    height: 42px;
    background: #333;
    transition: all 0.6s ease-in-out;
    &.on {
        margin-top: -42px;
    }
    .container {
        width: 100%;
        max-width: 1050px;
        margin: 0 auto;
        text-align: center;
        position: relative;
        display: flex;
        h1 {
            width:100%;
            line-height: 42px;
            color: #fff;
            font-size: 14px;
            font-weight: 500;
            strong {
                font-weight: 600;
            }
            
        }
        button {
                position: absolute;
                right: 0;
                width: 25px;
                height: 25px;
                opacity: 0.6;
            }
    }
}